.teacher{
    border: 1px solid white;
    margin:50px;
    text-align: center;
    border-radius: 10px;
    
  }
  .teacher img{
      width: 200px;
      height: 200px;
      border-radius: 50%;
  }
  .teacher:hover{
      box-shadow: 10px 10px 5px #aaaaaa;
      transform: scale(0.9,0.9);
  }